import React from "react";
import { Flex, Text } from "@chakra-ui/react";
import { SchemaPartInstruction } from "../utils/types";

interface Props {
  parts: SchemaPartInstruction[];
}

export default function PartTextList({ parts }: Props) {
  return (
    <Flex direction="column" gap={2}>
      {parts.map((item, i) => (
        <React.Fragment key={i}>
          {item.group != null && (
            <Text fontWeight="bold" mt={i > 0 ? 4 : 0}>
              {item.group}
            </Text>
          )}
          <Flex flexWrap="wrap" gap={2} align="top">
            <Text
              variant={"code-highlighted"}
              width="fit-content"
              height="fit-content"
            >
              Part {`${i + 1}`}
            </Text>
            <Text>
              ({item.direction}) {item.instruction}
            </Text>
          </Flex>
        </React.Fragment>
      ))}
    </Flex>
  );
}
