import { VestSchema } from "@tanyoknits/shared";
import OverviewWithSchema from "../../components/OverviewWithSchema";
import {
  topDownOverview,
  vestPartsInstruction,
} from "../../patternMakers/vest";
import PageContentWrapper from "../../layout/PageContentWrapper";
import { PatternBySizeProps } from "../../layout/PatternBySize";

export default function Instruction({ menu, size }: PatternBySizeProps) {
  let content = <div>Under construction</div>;
  if (menu === 0) {
    // overview with motif repeats
    content = (
      <OverviewWithSchema
        overview={topDownOverview}
        parts={vestPartsInstruction}
      >
        <VestSchema viewBoxRatio={1.6} neck="v" />
      </OverviewWithSchema>
    );
  }
  return <PageContentWrapper>{content}</PageContentWrapper>;
}
